import styled from "styled-components";
import theme from "../../Theme";

const Title = styled.h1`
    text-align: center;
    font-family: 'Aboreto';
    font-size: 5rem;
    color: ${theme.quaternary};

    @media(max-width: 59.5rem){
        font-size: 2.5rem;
        padding-top: .5rem;
    }
`;

export default Title;