import styled from "styled-components";
import Background from '../img/background_secundary.png';

const ContainerBackgroundSecundary = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    text-align: center;
    height: ${(props) => props.primaryHeight ? '110vh' : '88vh'};
    background-image: url(${Background});
    background-repeat: no-repeat;
    background-size: cover;
`;

export default ContainerBackgroundSecundary;