import styled from "styled-components";
import theme from "../../Theme";

const TitleSections = styled.h1`
    font-family: 'Rubik Marker Hatch';
    padding-top: 3rem;
    padding-bottom: ${(props) => props.primary ? '12rem' : '0'};
    color: ${(props) => props.primaryColor ? theme.quinary : theme.quaternary};

    @media(max-width: 60rem){
        padding: ${(props) => props.padding ? '6rem' : '1rem'};
        padding-bottom: ${(props) => props.primaryPadding ? '0.5rem' : '0'}; //6.8rem
        padding-bottom: ${(props) => props.skill ? '6.8rem' : '0'};
    }
`;

export default TitleSections;