import React from 'react';
import Navbar from '../elements/Menu/Navbar';
import LabelMenu from '../elements/Menu/LabelMenu';
import UlMenu from '../elements/Menu/UlMenu';
import LiMenu from '../elements/Menu/LiMenu';
import AMenu from '../elements/Menu/AMenu';
import CheckboxMenu from '../elements/Menu/CheckboxMenu';
import {LabelSvg, SvgMenu} from '../elements/Menu/SvgMenu';
import Logo from '../img/logo.png';


function Menu() {
    return ( 
        <Navbar>
            <CheckboxMenu type='checkbox' id='check' check ></CheckboxMenu>
            <LabelSvg htmlFor='check'>
                <SvgMenu />
            </LabelSvg>
            <LabelMenu><img src={Logo} alt='' /></LabelMenu>
                <UlMenu>
                    <LiMenu><AMenu active href='/'>Home</AMenu></LiMenu>
                    <LiMenu><AMenu href='/aboutme'>About Me</AMenu></LiMenu>
                    <LiMenu><AMenu href='/skills'>Skills</AMenu></LiMenu>
                    <LiMenu><AMenu href='/portfolio'>Portfolio</AMenu></LiMenu>
                </UlMenu>
        </Navbar>
        
     );
}

export default Menu;