import React from 'react';
import ContainerFooter from '../elements/Footer/ContainerFooter';
import { FooterContainer, FooterIcon, GithubIcon, InstagramIcon, LinkedinIcon } from '../elements/Footer/IconsFooter';
import LabelMenu from '../elements/Menu/LabelMenu';
import Logo from '../img/logo.png';

function Footer() {
    return ( 
        <ContainerFooter>
            <LabelMenu footer><img src={Logo} alt='' /></LabelMenu>
                <FooterContainer>
                    <a href='https://www.instagram.com/hikenjos/' rel='noreferrer noopener nofollow' target='_blank'>
                        <FooterIcon>
                            <InstagramIcon/>
                        </FooterIcon>
                    </a>
                    <a href='https://www.linkedin.com/in/jos%C3%A9-sanjuanelo-sandoval-8198671a0/' rel='noreferrer noopener nofollow' target='_blank'>
                            <FooterIcon>
                            <LinkedinIcon/>
                        </FooterIcon>
                    </a>
                    <a href='https://github.com/HikenJos' rel='noreferrer noopener nofollow' target='_blank'>
                        <FooterIcon>
                            <GithubIcon/>
                        </FooterIcon>
                    </a>
                </FooterContainer>
        </ContainerFooter>
    );
}

export default Footer;