import styled from "styled-components";
import theme from "../../Theme";

const Description = styled.p`
    width: 50%;
    margin: 0 auto;
    font-family: 'Aboreto';
    font-weight: 600;
    padding: 4rem 0;
    color: ${theme.quinary};

    @media(max-width: 60rem){
        color: ${theme.quaternary};
    }
`;

export default Description;