import styled from "styled-components";
import theme from "../../Theme";

const Ul = styled.ul`
    float: right;
    margin-right: 1.25rem;

    @media (max-width: 53.625rem){
        position: fixed;
        width:100%;
        height: 100vh;
        background: ${theme.secundary};
        top: 5rem;
        left: -100%;
        text-align: center;
        transition: all .5s;
    }
`;

export default Ul;