import styled from "styled-components";
import PracticeEnglish from '../../img/practice_your_english.png';
import PracticeEnglish_B from '../../img/practice_your_english_b.png';
import IncomeAndExpensive from '../../img/income_and_expensive.png';
import IncomeAndExpensive_B from '../../img/income_and_expensive_b.png';
import CrudApi from '../../img/crudapi.png';
import CrudApi_B from '../../img/crudapi_b.png';
import theme from "../../Theme";

const Card = styled.div`
    width: 18.125rem;
    height: 13.75rem;
    border-radius: .6123rem;
    box-shadow: 15px 15px 30px rgba(25, 25, 25, 0.11),
                -15px -15px 30px rgba(60, 60, 60, 0.082);
    text-align: center;
    background: ${theme.secundary};
    overflow: hidden;
    transition: all ease-in-out .5s;
    transition-delay: .3s;
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 96%);
    :hover{
        height: 379px;
        padding: 0; 
    }

    @media(max-width: 60rem){
        width: 11.875rem;
        height: 8.75rem;
        transition: none;
        :hover{
            height: 8.75rem;
        }
    }
`;

const HeaderPracticeEnglish = styled.div`
    padding: 5.625rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-image: url(${PracticeEnglish});
    margin-bottom: 1rem;

    @media(max-width: 60rem){
        background-image: url(${PracticeEnglish_B});
    }
`;

const HeaderListExpenses = styled.div`
    padding: 5.625rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-image: url(${IncomeAndExpensive});
    margin-bottom: 1rem;

    @media(max-width: 60rem){
        background-image: url(${IncomeAndExpensive_B});
    }
`;

const HeaderCrudApi = styled.div`
    padding: 5.625rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-image: url(${CrudApi});
    margin-bottom: 1rem;

    @media(max-width: 60rem){
        background-image: url(${CrudApi_B});
    }
`;

const Title = styled.h1`
    font-family: 'Rubik Marker Hatch';
    font-size: 1.1em;
    letter-spacing: .1em;
    font-weight: 500;
    text-transform: uppercase;
    padding: 4px 0 14px 0;
    transition: all .5s;
    color: ${theme.primary};
`;

const Content = styled.div`
    display: block;
    text-align: justify;
    color: #212121;
    margin: 0 1.125rem;
`;

const DescriptionChipCard = styled.p`
    font-family: 'Aboreto';
    color: ${theme.quaternary};
`;

export {Card, HeaderPracticeEnglish, HeaderListExpenses, HeaderCrudApi, Title, Content, DescriptionChipCard};