import styled from "styled-components";

const ContainersPortfolio = styled.div`
    position: relative;
    width: 70%;
    height: 200vh;
    display: flex;
    justify-content: space-around;
    align-content: space-around;
    margin: 0 auto;
    padding-bottom: 2rem;
    flex-wrap: wrap;

    @media(max-width: 60rem){
        padding-top: 3rem;
    }
`;

export {ContainersPortfolio};