import styled from "styled-components";
import theme from "../../Theme";

const Label = styled.label`
    color: ${theme.quaternary};
    font-size: 2.188rem;
    line-height: ${(props) => props.footer ? '0' : '5rem'};
    padding: ${(props) => props.footer ? '0' : '0 6.25rem'};
    font-weight: bold;

    img {
        width: 10rem;
    }

    @media (max-width: 59.5rem){
        font-size: ${(props) => props.footer ? '2.188rem' : '1.875rem;'};
        padding-left: ${(props) => props.footer ? '0' : '3.125rem;'};
        padding-top: ${(props) => props.footer ? '0.627rem' : '0'};
        img {
            width: 6rem;
            top: 0rem;
        }
    }
`;

export default Label;