import React from 'react';
import { Helmet } from 'react-helmet';
import ContainerBackgroundPrimary from '../elements/ContainerBackgroundPrimary';
import TitleSections from '../elements/Sections/TitleSection';
import ChipCardSkill from './ChipCardSkill';

function Skills() {
    return ( 
        <>
            <Helmet>
                <title>Skills</title>
            </Helmet>
            <ContainerBackgroundPrimary primaryContent>
                <TitleSections primary primaryColor primaryPadding skill>Skills</TitleSections>
                <ChipCardSkill/>
            </ContainerBackgroundPrimary>
        </>
     );
}

export default Skills;