import styled from "styled-components";
import {ReactComponent as FilledStar} from '../../img/filled_star.svg';
import {ReactComponent as EmptyStar} from '../../img/empty_star.svg';

const Stars = styled.div`
    font-size: 1rem;
`;

const SVGFilledStar = styled(FilledStar)`
    width: 2rem;
    fill: gold;
    padding: 0 .1rem;
`;

const SVGEmptyStar = styled(EmptyStar)`
    width: 2rem;
    fill: gold;
    padding: 0 .1rem
    `;


export {Stars, SVGFilledStar, SVGEmptyStar}