import CapsuleChipCardSkills from "../elements/Skills/CapsuleChipCardSkills";
import ContainerSkills from "../elements/Skills/ContainerSkills";
import Icon from "../elements/Skills/Icon";
import Name from "../elements/Skills/Name";
import {Stars, SVGFilledStar, SVGEmptyStar} from "../elements/Skills/Stars";
