import styled from 'styled-components';
import { ReactComponent as Menu} from '../../img/menulnd.svg';

const LabelSvg = styled.label`
    width: 1.875rem;
    float: right;
    line-height: 5rem;
    margin-right: 2.5rem;
    cursor: pointer;
    display: none;

    @media (max-width: 53.625rem){
        display: block;
    }
`;

const SvgMenu = styled(Menu)`
    width: 2rem;
`;

export {LabelSvg, SvgMenu};