import styled from "styled-components";
import Background from '../img/background_primary.png';

const ContainerBackgroundPrimary = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    text-align: center;
    height: ${(props) => props.primaryContent ? 'auto' : '100vh'};
    background-image: url(${Background});
    background-repeat: no-repeat;
    background-size: cover;
`;

export default ContainerBackgroundPrimary;