import React from 'react';
import ContainerBackgroundPrimary from '../elements/ContainerBackgroundSecundary';
import {ContainersPortfolio} from '../elements/Portfolio/ContainersPortfolio';
import { Card, Content, DescriptionChipCard, HeaderCrudApi, HeaderListExpenses, HeaderPracticeEnglish, Title } from '../elements/Portfolio/ChipCardPortfolioB';
import TitleSections from '../elements/Sections/TitleSection';
import SubtitleSection from '../elements/Sections/SubtitleSection';
import { Helmet } from 'react-helmet';

function Portfolio() {
    return ( 
        <>
            <Helmet>
                <title>Portfolio</title>
            </Helmet>
            <ContainerBackgroundPrimary displayMovil primaryContent primaryHeight>
                <TitleSections primaryPadding>Porfolio</TitleSections>
                <SubtitleSection>My Projects</SubtitleSection>
                <ContainersPortfolio>
                    <a href='https://palabras-en-ingles-fae0b.web.app/' rel='noreferrer noopener nofollow' target='_blank'>
                        <Card>
                            <HeaderPracticeEnglish>
                                <Title>Remeber Words</Title>
                            </HeaderPracticeEnglish>
                            <Content>
                                <DescriptionChipCard>App to remember words and practice English a few minutes a day.</DescriptionChipCard><br/>
                                <DescriptionChipCard>React + Firebase.</DescriptionChipCard>
                            </Content>
                        </Card>
                    </a>
                    <a href='https://curso-react-app-lista-ga-59cd6.web.app/' rel='noreferrer noopener nofollow' target='_blank'>
                        <Card>
                            <HeaderListExpenses>
                                <Title>List Expenses</Title>
                            </HeaderListExpenses>
                            <Content>
                                <DescriptionChipCard>App to keep track of expenses made daily.</DescriptionChipCard><br/>
                                <DescriptionChipCard>React + Firebase.</DescriptionChipCard>
                            </Content>
                        </Card>
                    </a>
                    <a href='https://cliente-curso-udemy.web.app/' rel='noreferrer noopener nofollow' target='_blank'>
                        <Card>
                            <HeaderCrudApi>
                                <Title>CRUD API - LIST INCIDENTS</Title>
                            </HeaderCrudApi>
                            <Content>
                                <DescriptionChipCard>App to keep track of expenses made daily.</DescriptionChipCard><br/>
                                <DescriptionChipCard>Node.js, Express, Bootstrap, MongoDB</DescriptionChipCard>
                            </Content>
                        </Card>
                    </a>
                </ContainersPortfolio>
            </ContainerBackgroundPrimary>
        </>
     );
}

export default Portfolio;