import React from 'react';
import { Helmet } from 'react-helmet';
import AIcons from '../elements/Aboutme/AIcons';
import Description from '../elements/Aboutme/Description';
import ContainerBackgroundSecunadary from '../elements/ContainerBackgroundSecundary';
import SubtitleSection from '../elements/Sections/SubtitleSection';
import TitleSections from '../elements/Sections/TitleSection';
import {ReactComponent as Instagram} from '../img/instagram.svg';
import {ReactComponent as Linkedin} from '../img/linkedin.svg';



function AboutMe() {
    return ( 
        <>
            <Helmet>
                <title>About Me</title>
            </Helmet>
            <ContainerBackgroundSecunadary>
                <TitleSections>About Me</TitleSections>
                <SubtitleSection>Who am i?</SubtitleSection>
                <Description>professional web developer and systems engineer with solid knowledge in development technologies, music lover, dedicated and always in constant growth and desire to learn</Description>
                <div><AIcons rel='noreferrer noopener nofollow' href='https://www.instagram.com/hikenjos/' target='_blank'><Instagram /></AIcons ><AIcons rel='noreferrer noopener nofollow' href='https://www.linkedin.com/in/jos%C3%A9-sanjuanelo-sandoval-8198671a0/' target='_blank'><Linkedin /></AIcons></div>
            </ContainerBackgroundSecunadary>
        </>

     );
}

export default AboutMe;